import { Slide, SlideTheme } from '@/types/slides'
import { CreatingElement } from '@/types/edit'
import { ToolbarState } from '@/types/toolbar'
import { slides } from '@/mocks/slides'
import { theme } from '@/mocks/theme'
import { SYS_FONTS } from '@/configs/font'

export interface State {
  activeElementIdList: string[];
  handleElementId: string;
  activeGroupElementId: string;
  canvasPercentage: number;
  canvasScale: number;
  thumbnailsFocus: boolean;
  editorAreaFocus: boolean;
  disableHotkeys: boolean;
  showGridLines: boolean;
  creatingElement: CreatingElement | null;
  availableFonts: typeof SYS_FONTS;
  toolbarState: ToolbarState;
  theme: SlideTheme;
  viewportRatio: number;
  slides: Slide[];
  slideIndex: number;
  selectedSlidesIndex: number[];
  snapshotCursor: number;
  snapshotLength: number;
  ctrlKeyState: boolean;
  shiftKeyState: boolean;
  screening: boolean;
  clipingImageElementId: string;
}

export const state: State = {
  activeElementIdList: [], // ID handleElementId
  handleElementId: '', // ID
  activeGroupElementId: '', // ID
  canvasPercentage: 90, // 
  canvasScale: 1, // 1000px
  thumbnailsFocus: false, // 
  editorAreaFocus: false, //  
  disableHotkeys: false, // 
  showGridLines: false, // 
  creatingElement: null, // 
  availableFonts: [], // 
  toolbarState: 'slideStyle', // 
  viewportRatio: 0.5625, // 16:9
  theme: theme, // 
  slides: slides, // 
  slideIndex: 0, // 
  selectedSlidesIndex: [], // 
  snapshotCursor: -1, // 
  snapshotLength: 0, // 
  ctrlKeyState: false, // ctrl
  shiftKeyState: false, // shift
  screening: false, // 
  clipingImageElementId: '', // ID  
}